import { Component } from "react";


class App extends Component {
   state = {
      date: '11.11.2018',
      arr: ['t', 's', 'a']
   }

   render() {
      return (
         <div className='body'>
         {}
            <div className="bands">
               <div className="band"></div>
               <div className="band"></div>
               <div className="band"></div>
               <div className="band"></div>
            </div>
            <div className="vertical-date">
               <ul>
                  {this.state.date.split('').map((val, key) =>
                     <li>{val}</li>
                  )}
               </ul>
            </div>

            <div className="dates">
               {this.state.arr.map((val, key) =>
                  <div className='date'>this.state.date</div>
               )}
            </div>
            <div className="logo"></div>
            <div className="center">
               <div className="text">Comming Soon</div>
               <input type="text" name="email" id="email" />
               <div className="input-text">Enter your email and well holla at you when making is made.</div>
            </div>


            <style jsx>{`
            .body{
               width: 100%;
               height: 95vh;
               display:flex;
               justify-content: center;
               align-items: center;
               font-family: Helvetica Nueu;
            }

            .bands, .logo, .dates, .vertical-date {
               position: absolute;
            }

            .vertical-date {
               left:15px;
               top: 0;
               font-size: 700px;
               height: 100%;
               display: flex;
               align-items:center;
            }

            .vertical-date ul {
               list-style-type: none;
               margin:0;
               padding:0;
            }

            .dates {
               bottom: 0;
               right: 0;
            }

            .center {
               text-align: center;
            }

            .text {
               font-size: 80px;
            }

            input[type=text] {
               width: 80%;
               margin: 0 auto;
               padding: 10px;
            }
            

         `}</style>
         </div>
      );
   }
}

export default App;